.details {
  &__title {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 64px;
    }

    @include onDesktop {
      margin-bottom: 48px;
    }
  }

  &__container {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }
  }

  &__button-wrapper {
    @include pageGrid;
  }

  &__button {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 5 / 9;
    }
  }
}