.contacts {
  &__content {
    @include pageGrid;
    row-gap: 48px;
  }

  &__form {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 24px 0;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 8 / -1;
    }
  }

  &__title {
    margin-bottom: 48px;
  }

  &__contact-title {
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
  }

  &__contact-detail {
    color: $main-text-color;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;

    transition: color $effectDuration;

    &:hover {
      color: orange;
    }
  }
}