.nav {
  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    position: relative;
    color: $accent-main-color;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    @include onDesktop {
      font-weight: 700;
    }

    &::after {
      content: '';
      display: block;
      position: absolute;
      bottom: -7px;
      height: 1px;
      width: 100%;
      background-color: $accent-main-color;
      
      transition: transform $effectDuration;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}