.about-us {
  display: flex;
  flex-direction: column;
  gap: 32px;

  @include onTablet {
    @include pageGrid;
  }

  &__title {
    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }

  &__description {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 6 / 12;
    }
  }
}