.section-title {
  font-weight: 500;
  font-size: 32px;
  line-height: 100%;
  letter-spacing: -1.5px;
  color: $main-text-color;
  text-align: center;
  text-transform: capitalize;

  &--text-align--initial {
    text-align: initial;
  }

  @include onTablet {
    font-size: 48px;
  }

  @include onDesktop {
    font-size: 64px;
  }
}