.product {
  &__image-wrapper {
    display: flex;
    margin-bottom: 32px;
  }

  &__image {
    width: 100%;
    @include hover(transform, scale(1.05));
  }

  &__title {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }

  &__description {
    margin-bottom: 16px;
  }

  &__price {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }
}