.form {
  display: flex;
  flex-direction: column;
  gap: 16px;

  &__field {
    box-sizing: border-box;
    font-family: Poppins, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    padding: 14px 16px;
    outline: none;
    border-radius: 40px;
    border: 1px solid transparent;
    background-color: $form-bg-color;

    &:-webkit-autofill {
      transition: background-color 999999s;
      -webkit-text-fill-color: #292929;
    }

    &::placeholder {
      font-family: Poppins, sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #7E7E83;
    }

    &:hover {
      border-color: #D5D7DE;
    }

    &:focus {
      border-color: #292929;
    }

    &--textarea {
      height: 148px;
      border-radius: 16px;
      resize: none;
      margin-bottom: 16px;
    }
  }
}