.detail {
  &__photos {
    margin-bottom: 16px;
    @include pageGrid;
  }

  &__link {
    position: relative;
    overflow: hidden;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(transform, scale(1.1));
  }

  &__title {
    margin-bottom: 8px;
    font-size: 24px;
    line-height: 140%;
  }
}