.menu {
  background-color: $menu-bg-color;
  height: 100vh;
  font-weight: 600;
  text-transform: uppercase;

  &__content {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onTablet {
      gap: 8px;
    }

    @include onDesktop {
      gap: 24px;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 169%;
    color: $accent-main-color;
  }

  &__book-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $accent-main-color;

    font-size: 12px;
    line-height: 133%;
    letter-spacing: 2px;
    color: $accent-main-color;
  }
}