.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-block: 24px;

  @include onTablet {
    padding-block: 36px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    width: 130px;

    @include hover(transform, scale(1.1));

    @include onTablet {
      width: 187px;
    }
  }

  &__icons {
    display: flex;
    gap: 24px;

    @include onTablet {
      gap: 32px;
    }
  }
}