.products {
  &__title {
    margin-bottom: 48px;

    @include onDesktop {
      margin-bottom: 56px;
    }
  }

  &__container {
    @include pageGrid;
    row-gap: 40px;
  }

  &__product {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2 / -2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }
}