.button {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 56px;
  width: 100%;
  background-color: $main-text-color;
  border-radius: 40px;
  color: $accent-main-color;
  font-weight: 700;
  line-height: 27px;
  transition: background-color $effectDuration;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}